import API_ENDPOINT from '../globals/api-endpoint';

class RecommendationDbSource {
  static async getAdminAllRecommendations() {
    const token = localStorage.getItem('token');
    const response = await fetch(API_ENDPOINT.RECOMMENDATIONS + '/admin', {
      method: 'GET',
      headers: {
        'Authorization': `Bearer ${token}`
      },
    });

    return response.json();
  }

  static async getAllRecommendations() {
    const response = await fetch(API_ENDPOINT.RECOMMENDATIONS);
    const responseJson = await response.json();
    return responseJson;
  }

  static async getProductById(id) {
    const response = await fetch(API_ENDPOINT.RECOMMENDATION_ID(id));
    const responseJson = await response.json();
    return responseJson;
  }

  static async addRecommendation(productData) {
    const token = localStorage.getItem('token');
    const formData = new FormData();
    formData.append('name', productData.name);
    formData.append('description', productData.description);
    formData.append('price', productData.price);
    formData.append('status', productData.status);
    formData.append('productImage', productData.productImage);

    const response = await fetch(API_ENDPOINT.RECOMMENDATIONS, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${token}`
      },
      body: formData,
    });

    return response.json();
  }

  static async updateRecommendation(id, productData) {
    const token = localStorage.getItem('token');
    const formData = new FormData();
    formData.append('name', productData.name);
    formData.append('description', productData.description);
    formData.append('status', productData.status);
    formData.append('price', productData. price);
    formData.append('productImage', productData.productImage);

    const response = await fetch(API_ENDPOINT.RECOMMENDATION_ID(id), {
      method: 'PUT',
      headers: {
        'Authorization': `Bearer ${token}`
      },
      body: formData,
    });

    return response.json();
  }

  static async deleteRecommendation(id) {
    const token = localStorage.getItem('token');
    const response = await fetch(API_ENDPOINT.RECOMMENDATION_ID(id), {
      method: 'DELETE',
      headers: {
        'Authorization': `Bearer ${token}`
      },
    });

    return response.json();
  }
}

export default RecommendationDbSource;
