  padding: 0;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand .brand-name {
  color: #8B4512;
  text-transform: uppercase;
  font-size: .9rem;
  user-select: none;
  margin-left: .5rem;
}

.app-bar .app-bar__brand img {
  height: 30px;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 56px;
  left: -180px;
  width: 150px;
  height: 100vh;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #333;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
}

main {
  flex: 1;
}

.hero {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 56px);
  background-color: #8B451330;
  gap: 10px;
}

.hero__content {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 32px;
  gap: 15px;
  text-align: center;
  align-items: center;
}

.hero__content h2 {
  color: #8B4513;
  font-size: 1.4rem;
}

.hero__content p {
  color: #555;
  font-size: 1rem;
}

.hero__content a {
  background-color: #8B4513;
  border: none;
  text-decoration: none;
  color: #fff;
  border-radius: .5rem;
  width: fit-content;
  padding: 10px 20px;
  font-size: .8rem;
}

.hero__image {
  height: 50%;
  display: flex;
  align-items: end;
  justify-content: center;
  background-color: #8B451360;
  border-top-left-radius: 60%;
  border-top-right-radius: 60%;
}

.hero__image img {
  height: 80%;
  opacity: .95;
  filter: drop-shadow(0 0 30px rgba(0, 0,0, 0.6));
}

.products {
  padding: 30px 32px;
  background-color: rgb(250, 250, 250);
  border-bottom: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  min-height: 100vh;
}

#products__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.8rem;
}

#products__container .card-item {
  padding: 15px;
  border: 1px solid #ddd;
  box-shadow: 0 5px 8px rgba(0, 0,0, 0.1);
  border-radius: 1rem;
}

#products__container .card-item__header {
  background-color: rgb(250, 250, 250);
  margin-bottom: .5rem;
}

.card-item__content {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: .3rem;
}

.card-item__content h4 {
  color: #8B4513;
  font-size: 1.2rem;
}

.card-item__content p {
  color: #555;
  font-size: 1rem;
}

#products__container .card-item__header img {
  width: 100%;
  height: 15rem;
  border-radius: .6rem;
  object-fit: contain;
  background-color: white;
}

.location {
  padding: 30px 32px;
  background-color: rgb(250, 250, 250);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.headers {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.headers h3 {
  font-size: 1.1rem;
  color: #555;
}

.headers h2 {
  font-size: 1.6rem;
  color: #8B4513;
}

.location{
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.location .container {
  display: flex;
  flex-direction: column;
  gap: 1.3rem;
}

.location__map iframe {
  width: 100%;
  height: 15rem;
  border: 0;
}

.location__information {
  display: flex;
  flex-direction: column;
  gap: .2rem;
  justify-content: center;
}

.location__information h3 {
  color: #8B4513;
  font-size: 1.5rem;
  margin-bottom: .9rem;
}

.location__information h4 {
  color: #8B4513;
}

.location__information a {
  text-decoration: none;
  color: #555;
  margin: 5px 0;
}

.location__information a:hover {
  text-decoration: underline;
  color: #8B4513;
}

.fixed.fa-whatsapp {
  position: fixed;
  right: 20px;
  bottom: 20px;
  background-color: #25d366;
  color: white;
  font-size: 2rem;
  padding: .9rem;
  border-radius: 50%;
}

footer .box-container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  padding: 65px 32px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}

footer .box-container .box h3 {
  padding: .5rem 0;
  font-size: 1.3rem;
  color: #8B4513;
}

footer .box-container .box a {
  display: block;
  padding: .2rem 0;
  font-size: .9rem;
  color: #555;
  text-decoration: none;
}

footer .box-container .box a:hover {
  color: #8B4513;
  text-decoration: underline;
}

.footer__bottom {
  padding: 25px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__bottom p {
  color: #555;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,UAAU;EACV,SAAS;EACT,4DAA4D;EAC5D,sBAAsB;AACxB;;AAEA;EACE,kCAAkC;EAClC,uBAAuB;EACvB,iBAAiB;EACjB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,iBAAiB;EACjB,uBAAuB;EACvB,aAAa;EACb,+BAA+B;EAC/B,SAAS;EACT,gBAAgB;EAChB,MAAM;EACN,WAAW;AACb;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,yBAAyB;EACzB,gBAAgB;EAChB,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,YAAY;EACZ,YAAY;EACZ,aAAa;EACb,oBAAoB;EACpB,YAAY;EACZ,uBAAuB;EACvB,gBAAgB;AAClB;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,qBAAqB;EACrB,qBAAqB;EACrB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,0BAA0B;EAC1B,2BAA2B;EAC3B,SAAS;AACX;;AAEA;EACE,WAAW;EACX,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,eAAe;EACf,SAAS;EACT,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,qBAAqB;EACrB,WAAW;EACX,oBAAoB;EACpB,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,gBAAgB;EAChB,uBAAuB;EACvB,2BAA2B;EAC3B,2BAA2B;EAC3B,4BAA4B;AAC9B;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,+CAA+C;AACjD;;AAEA;EACE,kBAAkB;EAClB,oCAAoC;EACpC,6BAA6B;EAC7B,aAAa;EACb,sBAAsB;EACtB,WAAW;EACX,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,0BAA0B;EAC1B,WAAW;AACb;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uCAAuC;EACvC,mBAAmB;AACrB;;AAEA;EACE,oCAAoC;EACpC,oBAAoB;AACtB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,cAAc;EACd,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,eAAe;AACjB;;AAEA;EACE,WAAW;EACX,aAAa;EACb,oBAAoB;EACpB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,oCAAoC;EACpC,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,iBAAiB;EACjB,WAAW;AACb;;AAEA;EACE,iBAAiB;EACjB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,WAAW;EACX,aAA