import API_ENDPOINT from '../globals/api-endpoint';

class GaleryDbSource {
  static async getAllGaleries() {
    const response = await fetch(API_ENDPOINT.GALERIES);
    const responseJson = await response.json();
    return responseJson;
  }

  static async getGaleryById(id) {
    const response = await fetch(API_ENDPOINT.GALERY_ID(id));
    const responseJson = await response.json();
    return responseJson;
  }

  static async addGalery(galeryData) {
    const token = localStorage.getItem('token');
    const formData = new FormData();
    formData.append('name', galeryData.name);
    formData.append('date', galeryData.date);
    formData.append('productImage', galeryData.productImage);

    const response = await fetch(API_ENDPOINT.GALERIES, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${token}`
      },
      body: formData,
    });

    return response.json();
  }

  static async deleteGalery(id) {
    const token = localStorage.getItem('token');
    const response = await fetch(API_ENDPOINT.GALERY_ID(id), {
      method: 'DELETE',
      headers: {
        'Authorization': `Bearer ${token}`
      },
    });

    return response.json();
  }
}

export default GaleryDbSource;
