import ProductDbSource from '../../../data/productdb-source';
import { createProductItemTemplate } from '../../templates/template-creator';

const Upcoming = {
  async render() {
    return `
      <section class="products">
        <div class="headers">
          <h3>Seluruh</h3>
          <h2>Produk Kami</h2>
        </div>

        <div id="products__container"></div>
      </section>
    `;
  },

  async afterRender() {
    const products = await ProductDbSource.getAllProducts();
    const productsContainer = document.querySelector('#products__container');
    products.forEach((product) => {
      productsContainer.innerHTML += createProductItemTemplate(product);
    });
  },
};

export default Upcoming;
