import ProductDbSource from '../../../data/productdb-source';
import CONFIG from '../../../globals/config';
import UrlParser from '../../../routes/url-parser';
import renderHeaderAndAuthHandler from '../../../utils/renderHeaderAndAuthHandler';

const Products = {
  async render() {
    return `
      <section class="table-container">
        <div class="table-container-header">
          <h1>Manajemen Produk</h1>
          <button class="button" id="showFormButton">Menambahkan Produk</button>
        </div>

        <div class="table-list-container">
          <h2>List Produk</h2>
          <table id="productsTable">
            <thead>
              <tr>
                <th>Foto</th>
                <th>Nama</th>
                <th>Deskripsi</th>
                <th>Harga</th>
                <th>Status</th>
                <th colspan="2">Aksi</th>
              </tr>
            </thead>
            <tbody>
              <!-- Products will be dynamically inserted here -->
            </tbody>
          </table>
        </div>
      </section>

      <section class="input-form" id="productFormSection">
        <form id="addProductForm">
          <div class="form__header">
            <h1>Menambahkan Produk</h1>
            <button type="button" id="closeFormButton">x</button>
          </div>
          <div class="form__body">
            <div class="form__body__input-group">
              <label for="productName">Nama Produk</label>
              <input type="text" id="productName" placeholder="Masukkan Nama Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="productDescription">Deskripsi Produk</label>
              <input type="text" id="productDescription" placeholder="Masukkan Deskripsi Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="productPrice">Harga Produk</label>
              <input type="number" id="productPrice" placeholder="Masukkan Harga Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="productImage">Gambar Produk</label>
              <input type="file" id="productImage" required>
            </div>
            <div class="form__body__input-group status">
              <label for="productStatus">Tampilkan Produk:</label>
              <input type="checkbox" id="productStatus">
            </div>
            <div class="form__body__button">
              <button type="submit" class="button">Konfirmasi</button>
            </div>
          </div>
        </form>
      </section>

      <section class="input-form" id="editProductFormSection">
        <form id="editProductForm">
          <div class="form__header">
            <h1>Edit Produk</h1>
            <button type="button" id="closeEditFormButton">x</button>
          </div>
          <div class="form__body">
            <div class="form__body__input-group">
              <label for="editProductName">Nama Produk</label>
              <input type="text" id="editProductName" placeholder="Masukkan Nama Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="editProductDescription">Deskripsi Produk</label>
              <input type="text" id="editProductDescription" placeholder="Masukkan Deskripsi Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="editProductPrice">Harga Produk</label>
              <input type="number" id="editProductPrice" placeholder="Masukkan Harga Produk" required>
            </div>
            <div class="form__body__input-group">
              <label for="editProductImage">Gambar Produk</label>
              <input type="file" id="editProductImage">
            </div>
            <div class="form__body__input-group status">
              <label for="editProductStatus">Tampilkan Produk:</label>
              <input type="checkbox" id="editProductStatus">
            </div>
            <div class="form__body__button">
              <button type="submit" class="button">Simpan</button>
            </div>
          </div>
        </form>
      </section>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const productFormSection = document.getElementById('productFormSection');
    const editProductFormSection = document.getElementById('editProductFormSection');
    const showFormButton = document.getElementById('showFormButton');
    const closeFormButton = document.getElementById('closeFormButton');
    const closeEditFormButton = document.getElementById('closeEditFormButton');
    const addProductForm = document.getElementById('addProductForm');
    const editProductForm = document.getElementById('editProductForm');
    const productsTable = document.getElementById('productsTable').getElementsByTagName('tbody')[0];

    // Event listener to show the add form
    showFormButton.addEventListener('click', () => {
      productFormSection.classList.add('open');
    });

    // Event listener to hide the add form
    closeFormButton.addEventListener('click', () => {
      productFormSection.classList.remove('open');
    });

    // Event listener to hide the edit form
    closeEditFormButton.addEventListener('click', () => {
      editProductFormSection.classList.remove('open');
    });

    const fetchProducts = async () => {
      const products = await ProductDbSource.getAdminAllProducts();
      productsTable.innerHTML = products.map(product => {
        const formattedPrice = product.price.toLocaleString('id-ID', {
          style: 'currency',
          currency: 'IDR',
          minimumFractionDigits: 0
        });
      
        return `
          <tr>
            <td><img src="${CONFIG.BASE_IMAGE_URL + product.imageUrl}" alt="${product.name}"></td>
            <td>${product.name}</td>
            <td>${product.description}</td>
            <td>${formattedPrice}</td>
            <td>${product.status ? 'Ditampilkan' : 'Disembunyikan'}</td>
            <td>
              <button class="fa fa-pencil edit button" data-id="${product._id}"></button>
            </td>
            <td>
              <button class="fa-solid fa-trash-can delete button" data-id="${product._id}"></button>
            </td>
          </tr>
        `;
      }).join('');      
    };

    addProductForm.addEventListener('submit', async (event) => {
      event.preventDefault();

      renderHeaderAndAuthHandler(url);
    
      const productData = {
        name: document.getElementById('productName').value,
        description: document.getElementById('productDescription').value,
        price: document.getElementById('productPrice').value,
        status: document.getElementById('productStatus').checked,
        productImage: document.getElementById('productImage').files[0],
      };
    
      await ProductDbSource.addProduct(productData);
      await fetchProducts();
      productFormSection.classList.remove('open');
    });

    productsTable.addEventListener('click', async (event) => {
      const target = event.target;
      const productId = target.getAttribute('data-id');

      if (target.classList.contains('delete')) {
        await ProductDbSource.deleteProduct(productId);
        renderHeaderAndAuthHandler(url);
        await fetchProducts();
      }

      if (target.classList.contains('edit')) {
        const productId = target.getAttribute('data-id');
        const productRow = target.closest('tr');
        const productName = productRow.cells[1].innerText;
        const productDescription = productRow.cells[2].innerText;
        let productPrice = productRow.cells[3].innerText;
        productPrice = productPrice.replace(/Rp[\.\s]?/gi, "").replace(/\./g, "");
        productPrice = parseInt(productPrice, 10);

        document.getElementById('editProductName').value = productName;
        document.getElementById('editProductDescription').value = productDescription;
        document.getElementById('editProductPrice').value = productPrice;
        document.getElementById('editProductStatus').checked = productStatus;
        editProductFormSection.classList.add('open');

        editProductForm.onsubmit = async (e) => {
          e.preventDefault();
          renderHeaderAndAuthHandler(url);

          const updatedProductData = {
            name: document.getElementById('editProductName').value,
            description: document.getElementById('editProductDescription').value,
            price: document.getElementById('editProductPrice').value,
            status: document.getElementById('editProductStatus').checked,
            productImage: document.getElementById('editProductImage').files[0],
          };

          await ProductDbSource.updateProduct(productId, updatedProductData);
          await fetchProducts();
          editProductFormSection.classList.remove('open');
        };
      }
    });

    await fetchProducts();
  },
};

export default Products;
