import CONFIG from '../../globals/config';

const createProductItemTemplate = (batik) => {
  const formattedPrice = batik.price ? batik.price.toLocaleString('id-ID', {
    style: 'currency',
    currency: 'IDR',
    minimumFractionDigits: 0
  }) : '';

  return `
    <div class="card-item">
      <div class="card-item__header">
        <img class="card-item__header__poster" alt="${batik.title || ''}"
          src="${CONFIG.BASE_IMAGE_URL + batik.imageUrl}">
      </div>
      <div class="card-item__content">
        ${batik.name ? `<h4>${batik.name}</h4>` : ''}
        ${batik.description ? `<p>${batik.description}</p>` : ''}
        ${formattedPrice ? `<p>${formattedPrice}</p>` : ''}
      </div>
    </div>
  `;
};


export { createProductItemTemplate };
