import CONFIG from "../../../globals/config";

const Setting = {
  async render() {
    return `
      <section class="setting">
        <div class="switch-container">
          <div class="switch-header">
            <h1>Pengaturan</h1>
          </div>
          <div class="switch-group">
            <span class="switch-label">Tampilkan Nama</span>
            <label class="switch">
              <input id="toggleName" type="checkbox">
              <span class="slider round"></span>
            </label>
          </div>
          <div class="switch-group">
            <span class="switch-label">Tampilkan Deskripsi</span>
            <label class="switch">
              <input id="toggleDescription" type="checkbox">
              <span class="slider round"></span>
            </label>
          </div>
          <div class="switch-group">
            <span class="switch-label">Tampilkan Harga</span>
            <label class="switch">
              <input id="togglePrice" type="checkbox">
              <span class="slider round"></span>
            </label>
          </div>
        </div>

        <div class="input-container">
          <form>
            <div class="form__header">
              <h1>Ganti Password</h1>
            </div>

            <div class="form__body">
              <p class="form__body__error-message"></p>
              <p class="form__body__success-message"></p>
              <div class="form__body__input-group">
                <label for="password">Password</label>
                <input id="password" type="password" placeholder="Masukkan Password" required>
              </div>

              <div class="form__body__input-group">
                <label for="confirmPassword">Konfirmasi Password</label>
                <input id="confirmPassword" type="password" placeholder="Konfirmasi Password" required>
              </div>

              <div class="form__body__input-group">
                <label for="secretKey">Kata Kunci</label>
                <input id="secretKey" type="password" placeholder="Masukkan Kata Kunci" required>
              </div>
            </div>

            <div class="form__body__button">
              <a id="confirmButton" class="button" href="#">Konfirmasi</a>
            </div>
          </form>
        </div>
      </section>
    `;
  },

  async afterRender() {
    const toggleName = document.getElementById('toggleName');
    const toggleDescription = document.getElementById('toggleDescription');
    const togglePrice = document.getElementById('togglePrice');

    const fetchSettings = async () => {
      try {
        const response = await fetch(`${CONFIG.BASE_URL}settings/get`);
        const settings = await response.json();

        settings.forEach(setting => {
          if (setting.name === 'name') toggleName.checked = setting.value;
          if (setting.name === 'description') toggleDescription.checked = setting.value;
          if (setting.name === 'price') togglePrice.checked = setting.value;
        });
      } catch (error) {
        console.error('Error fetching settings:', error);
      }
    };

    const updateSetting = async (name, value) => {
      try {
        await fetch(`${CONFIG.BASE_URL}settings/update`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ name, value }),
        });
      } catch (error) {
        console.error('Error updating setting:', error);
      }
    };

    toggleName.addEventListener('change', () => updateSetting('name', toggleName.checked));
    toggleDescription.addEventListener('change', () => updateSetting('description', toggleDescription.checked));
    togglePrice.addEventListener('change', () => updateSetting('price', togglePrice.checked));

    await fetchSettings();

    const password = document.getElementById('password');
    const confirmPassword = document.getElementById('confirmPassword');
    const secretKey = document.getElementById('secretKey');
    const confirmButton = document.getElementById('confirmButton');
    const errorMessage = document.querySelector('.form__body__error-message');
    const successMessage = document.querySelector('.form__body__success-message');

    confirmButton.addEventListener('click', async (e) => {
      try {
        e.preventDefault();
        const response = await fetch(`${CONFIG.BASE_URL}admin/change-password`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ password: password.value, confirmPassword: confirmPassword.value, secretKey: secretKey.value }),
        });

        const responseJson = await response.json();


        if (!responseJson.success) {
          errorMessage.style.display = 'block';
          successMessage.style.display = 'none';
          errorMessage.innerText = responseJson.message;
        } else {
          successMessage.style.display = 'block';
          errorMessage.style.display = 'none';
          successMessage.innerText = 'Password berhasil diubah';
        }
      } catch (error) {
        errorMessage.style.display = 'block';
        successMessage.style.display = 'none';
        errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
      }
    });
  },
};

export default Setting;
