___CSS_LOADER_EXPORT___.push([module.id, `.galery {
  padding: 1rem 9%;
  background-color: rgb(250, 250, 250);
}

#jumbotronImage {
  width: 100%;
  height: 15rem;
  object-fit: contain;
  border: 3px solid white;
  border-radius: .4rem;
  background-color: #ddd;
}

.imageContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: white;
  border-radius: .4rem;
  max-height: 50vh;
  overflow-y: auto;
}

.img-item {
  width: 100%;
  height: 9rem;
  cursor: pointer;
  border: 3px solid white;
  border-radius: .4rem;
  object-fit: cover;
}

.img-item:hover,
.img-item.active {
  opacity: .5;
}

@media screen and (min-width: 650px) {
  #jumbotronImage {
    height: 23rem;
  }

  .imageContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 800px) {
  .imageContainer {
    grid-template-columns: repeat(4, 1fr);
  }

  .img-item {
    height: 13rem;
  }
}

@media screen and (min-width: 1200px) {
  .imageContainer {
    grid-template-columns: repeat(5, 1fr);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/galery.css"],"names":[],"mappings":"AAAA;EACE,gBAAgB;EAChB,oCAAoC;AACtC;;AAEA;EACE,WAAW;EACX,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,oBAAoB;EACpB,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,uBAAuB;EACvB,oBAAoB;EACpB,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,eAAe;EACf,uBAAuB;EACvB,oBAAoB;EACpB,iBAAiB;AACnB;;AAEA;;EAEE,WAAW;AACb;;AAEA;EACE;IACE,aAAa;EACf;;EAEA;IACE,qCAAqC;EACvC;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;;EAEA;IACE,aAAa;EACf;AACF;;AAEA;EACE;IACE,qCAAqC;EACvC;AACF","sourcesContent":[".galery {\r\n  padding: 1rem 9%;\r\n  background-color: rgb(250, 250, 250);\r\n}\r\n\r\n#jumbotronImage {\r\n  width: 100%;\r\n  height: 15rem;\r\n  object-fit: contain;\r\n  border: 3px solid white;\r\n  border-radius: .4rem;\r\n  background-color: #ddd;\r\n}\r\n\r\n.imageContainer {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  background-color: white;\r\n  border-radius: .4rem;\r\n  max-height: 50vh;\r\n  overflow-y: auto;\r\n}\r\n\r\n.img-item {\r\n  width: 100%;\r\n  height: 9rem;\r\n  cursor: pointer;\r\n  border: 3px solid white;\r\n  border-radius: .4rem;\r\n  object-fit: cover;\r\n}\r\n\r\n.img-item:hover,\r\n.img-item.active {\r\n  opacity: .5;\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n  #jumbotronImage {\r\n    height: 23rem;\r\n  }\r\n\r\n  .imageContainer {\r\n    grid-template-columns: repeat(3, 1fr);\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 800px) {\r\n  .imageContainer {\r\n    grid-template-columns: repeat(4, 1fr);\r\n  }\r\n\r\n  .img-item {\r\n    height: 13rem;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 1200px) {\r\n  .imageContainer {\r\n    grid-template-columns: repeat(5, 1fr);\r\n  }\r\n}"],"sourceRoot":""}]);
