import DrawerInitiator from '../utils/drawer-initiator';
import UrlParser from '../routes/url-parser';
import { userRoutes, adminRoutes } from '../routes/routes';
import '@fortawesome/fontawesome-free/css/all.min.css';
import renderHeaderAndAuthHandler from '../utils/renderHeaderAndAuthHandler';

class App {
  constructor({ button, drawer, content }) {
    this._button = button;
    this._drawer = drawer;
    this._content = content;

    this._initialAppShell();
  }

  _initialAppShell() {
    DrawerInitiator.init({
      button: this._button,
      drawer: this._drawer,
      content: this._content,
    });
  }

  _addFadeInAnimation() {
    this._content.classList.add('fade-in');
    this._content.addEventListener('animationend', () => {
      this._content.classList.remove('fade-in');
    }, { once: true });
  }

  _updateActiveLink(url) {
    const links = document.querySelectorAll('nav a');
    links.forEach(link => {
      if ((url === '/' && link.getAttribute('href') === '#/products') || link.getAttribute('href') === `#${url}`) {
        link.classList.add('open');
      } else {
        link.classList.remove('open');
      }
    });
  }

  async renderUserPage() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const page = userRoutes[url];

    this._addFadeInAnimation();
    this._updateActiveLink(url);

    this._content.innerHTML = await page.render();
    await page.afterRender();
  }

  async renderAdminPage() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const page = adminRoutes[url];

    renderHeaderAndAuthHandler(url);

    this._addFadeInAnimation();
    this._updateActiveLink(url);

    this._content.innerHTML = await page.render();
    await page.afterRender();
  }
}

export default App;
