import GaleryDbSource from '../../../data/galerydb-source';
import CONFIG from '../../../globals/config';

const GaleryUser = {
  async render() {
    return `
      <section class="galery">
        <div class="jumbotron">
          <img id="jumbotronImage" src="">
        </div>

        <div class="imageContainer" id="galleryContainer">
          <!-- Galeri akan ditambahkan secara dinamis di sini -->
        </div>
      </section>
    `;
  },

  async afterRender() {
    const galleryContainer = document.getElementById('galleryContainer');
    const jumbotronImage = document.getElementById('jumbotronImage');
    let nowActive = null;

    const displayImageInJumbotron = (imageUrl) => {
      jumbotronImage.src = imageUrl;
      jumbotronImage.classList.add('fade-in');
      setTimeout(() => jumbotronImage.classList.remove('fade-in'), 500);
    };

    const fetchAndRenderGallery = async () => {
      try {
        const galleryItems = await GaleryDbSource.getAllGaleries();
        galleryContainer.innerHTML = galleryItems.map(item => `
          <div class="galleryItem">
            <img src="${CONFIG.BASE_IMAGE_URL + item.imageUrl}" alt="${item.name}" class="img-item">
          </div>
        `).join('');

        const galleryImages = document.querySelectorAll('.galleryItem img');
        const firstImageUrl = CONFIG.BASE_IMAGE_URL + galleryItems[0].imageUrl;

        nowActive = galleryImages[0]
        nowActive.classList.add('active');

        displayImageInJumbotron(firstImageUrl);

        galleryImages.forEach(image => {
          image.addEventListener('click', () => {
            displayImageInJumbotron(image.src);
            if (nowActive) {
              nowActive.classList.remove('active');
            }
            image.classList.add('active');
            nowActive = image;
          });
        });
      } catch (error) {
        console.error('Error fetching gallery items:', error);
      }
    };

    await fetchAndRenderGallery();
  },
};

export default GaleryUser;
