import RecommendationDbSource from '../../../data/recommendationdb-source';
import CONFIG from '../../../globals/config';
import UrlParser from '../../../routes/url-parser';
import renderHeaderAndAuthHandler from '../../../utils/renderHeaderAndAuthHandler';

const Recommendations = {
  async render() {
    return `
      <section class="table-container">
        <div class="table-container-header">
          <h1>Manajemen Rekomendasi</h1>
          <button class="button" id="showFormButton">Menambahkan Rekomendasi</button>
        </div>
        <div class="table-list-container">
          <h2>List Rekomendasi</h2>

          <table>
            <thead>
              <tr>
                <th>Foto</th>
                <th>Nama</th>
                <th>Deskripsi</th>
                <th>Harga</th>
                <th>Status</th>
                <th colspan="2">Aksi</th>
              </tr>
            </thead>
            <tbody id="recommendationList">
              <!-- Recommendations will be dynamically inserted here -->
            </tbody>
          </table>
        </div>
      </section>

      <section id="recommendationFormSection" class="input-form">
        <form id="addRecommendationForm">
          <div class="form__header">
            <h1>Menambahkan Rekomendasi</h1>
            <button type="button" id="closeAddFormButton">x</button>
          </div>

          <div class="form__body">
            <p class="form__body__error-message"></p>
            <div class="form__body__input-group">
              <label for="recommendationName">Nama Rekomendasi:</label>
              <input type="text" id="recommendationName" placeholder="Masukkan Nama Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="recommendationDescription">Dekripsi Rekomendasi:</label>
              <input type="text" id="recommendationDescription" placeholder="Masukkan Deskripsi Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="recommendationPrice">Harga Rekomendasi:</label>
              <input type="number" id="recommendationPrice" placeholder="Masukkan Harga Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="recommendationImage">Recommendation Image:</label>
              <input type="file" id="recommendationImage" accept="image/*">
            </div>

            <div class="form__body__input-group status">
              <label for="recommendationStatus">Tampilkan Rekomendasi:</label>
              <input type="checkbox" id="recommendationStatus">
            </div>

            <div class="form__body__button">
              <button class="button" type="submit">Simpan</button>
            </div>
          </div>
        </form>
      </section>

      <section id="editRecommendationFormSection" class="input-form">
        <form id="editRecommendationForm">
          <div class="form__header">
            <h1>Edit Rekomendasi</h1>
            <button type="button" id="cancelEditRecommendation">x</button>
          </div>
          <div class="form__body">
            <input type="hidden" id="editRecommendationId">
            <p class="form__body__error-message"></p>

            <div class="form__body__input-group">
              <label for="editRecommendationName">Nama Rekomendasi:</label>
              <input type="text" id="editRecommendationName" placeholder="Masukkan Nama Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="editRecommendationDescription">Nama Rekomendasi:</label>
              <input type="text" id="editRecommendationDescription" placeholder="Masukkan Deskripsi Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="editRecommendationPrice">Harga Rekomendasi:</label>
              <input type="number" id="editRecommendationPrice" placeholder="Masukkan Harga Rekomendasi" required>
            </div>

            <div class="form__body__input-group">
              <label for="editRecommendationImage">Foto Rekomendasi:</label>
              <input type="file" id="editRecommendationImage" accept="image/*">
            </div>

            <div class="form__body__input-group status">
              <label for="editRecommendationStatus">Tampilkan Rekomendasi:</label>
              <input type="checkbox" id="editRecommendationStatus">
            </div>

            <div class="form__body__button">
              <button class="button" type="submit">Simpan</button>
            </div>
          </div>
        </form>
      </section>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const recommendationList = document.getElementById('recommendationList');
    const recommendationFormSection = document.getElementById('recommendationFormSection');
    const editRecommendationFormSection = document.getElementById('editRecommendationFormSection');
    const addRecommendationForm = document.getElementById('addRecommendationForm');
    const editRecommendationForm = document.getElementById('editRecommendationForm');

    // Fetch recommendations and render them initially
    const fetchAndRenderRecommendations = async () => {
      try {
        const recommendations = await RecommendationDbSource.getAdminAllRecommendations();
        recommendationList.innerHTML = recommendations.map(recommendation => {
          const formattedPrice = recommendation.price.toLocaleString('id-ID', {
            style: 'currency',
            currency: 'IDR',
            minimumFractionDigits: 0
          });
        
          return `
            <tr data-id="${recommendation._id}">
              <td><img src="${CONFIG.BASE_IMAGE_URL + recommendation.imageUrl}" alt="${recommendation.name}"></td>
              <td>${recommendation.name}</td>
              <td>${recommendation.description}</td>
              <td>${formattedPrice}</td>
              <td>${recommendation.status ? 'Ditampilkan' : 'Disembunyikan'}</td>
              <td>
                <button class="fa fa-pencil button edit"></button>
              </td>
              <td>
                <button class="fa-solid fa-trash-can button delete"></button>
              </td>
            </tr>
          `;
        }).join('');        
      } catch (error) {
        console.error('Error fetching recommendations:', error);
      }
    };

    // Event listener to show add recommendation form
    document.getElementById('showFormButton').addEventListener('click', () => {
      const errorMessage = document.querySelector('#addRecommendationForm .form__body__error-message');
      errorMessage.innerText = '';
      errorMessage.style.display = 'none';
      recommendationFormSection.classList.add('open');
    });

    // Event listener to close add recommendation form
    document.getElementById('closeAddFormButton').addEventListener('click', () => {
      recommendationFormSection.classList.remove('open');
    });

    // Event listener to show edit recommendation form
    recommendationList.addEventListener('click', (event) => {
      const target = event.target;
      if (target.classList.contains('edit')) {
        const recommendationRow = target.closest('tr');
        const recommendationId = recommendationRow.getAttribute('data-id');
        const recommendationName = recommendationRow.cells[1].innerText;
        const recommendationDescription = recommendationRow.cells[2].innerText;
        const errorMessage = document.querySelector('#editRecommendationForm .form__body__error-message');
        errorMessage.innerText = '';
        errorMessage.style.display = 'none';
        
        let recommendationPrice = recommendationRow.cells[3].innerText;
        recommendationPrice = recommendationPrice.replace(/Rp[\.\s]?/gi, "").replace(/\./g, "");
        recommendationPrice = parseInt(recommendationPrice, 10);
        
        const recommendationStatus = recommendationRow.cells[4].innerText === 'Ditampilkan';

        // Fill edit form fields with recommendation data
        document.getElementById('editRecommendationId').value = recommendationId;
        document.getElementById('editRecommendationName').value = recommendationName;
        document.getElementById('editRecommendationDescription').value = recommendationDescription;
        document.getElementById('editRecommendationPrice').value = recommendationPrice;
        document.getElementById('editRecommendationStatus').checked = recommendationStatus;
        editRecommendationFormSection.classList.add('open');
      }
    });

    // Event listener to close edit recommendation form
    document.getElementById('cancelEditRecommendation').addEventListener('click', () => {
      editRecommendationFormSection.classList.remove('open');
    });

    // Event listener to handle form submission for adding a recommendation
    addRecommendationForm.addEventListener('submit', async (event) => {
      event.preventDefault();
      const errorMessage = document.querySelector('#addRecommendationForm .form__body__error-message');
      
      renderHeaderAndAuthHandler(url);
      const productData = {
        name: document.getElementById('recommendationName').value,
        description: document.getElementById('recommendationDescription').value,
        price: document.getElementById('recommendationPrice').value,
        status: document.getElementById('recommendationStatus').checked,
        productImage: document.getElementById('recommendationImage').files[0],
      };

      try {
        const responseJson =  await RecommendationDbSource.addRecommendation(productData);

        if (responseJson.message === 'Maximum of 4 displayed recommendations reached') {
          errorMessage.style.display = 'block';
          errorMessage.innerText = 'Maksimal hanya 4 rekomendasi yang dapat ditampilkan';
          return;
        } else if (!responseJson.name) {
          errorMessage.style.display = 'block';
          errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
          return;
        }

        await fetchAndRenderRecommendations();
        recommendationFormSection.classList.remove('open');
      } catch (error) {
        const errorMessage = document.querySelector('#addRecommendationForm .form__body__error-message');
        errorMessage.style.display = 'block';
        errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
        console.error('Error adding recommendation:', error);
      }
    });

    // Event listener to handle form submission for editing a recommendation
    editRecommendationForm.addEventListener('submit', async (event) => {
      event.preventDefault();
      renderHeaderAndAuthHandler();
      const recommendationId = document.getElementById('editRecommendationId').value;
      const productData = {
        name: document.getElementById('editRecommendationName').value,
        description: document.getElementById('editRecommendationDescription').value,
        price: document.getElementById('editRecommendationPrice').value,
        status: document.getElementById('editRecommendationStatus').checked,
        productImage: document.getElementById('editRecommendationImage').files[0],
      };

      try {
        const errorMessage = document.querySelector('#editRecommendationForm .form__body__error-message');
        const responseJson = await RecommendationDbSource.updateRecommendation(recommendationId, productData);

        if (responseJson.message === 'Maximum of 4 displayed recommendations reached') {
          errorMessage.style.display = 'block';
          errorMessage.innerText = 'Maksimal hanya 4 rekomendasi yang dapat ditampilkan';
          return;
        } else if (!responseJson.name) {
          errorMessage.style.display = 'block';
          errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
          return;
        }

        await fetchAndRenderRecommendations();
        editRecommendationFormSection.classList.remove('open');
      } catch (error) {
        const errorMessage = document.querySelector('#editRecommendationForm .form__body__error-message');
        errorMessage.style.display = 'block';
        errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
        console.error('Error updating recommendation:', error);
      }
    });

    // Event listener to handle deletion of a recommendation
    recommendationList.addEventListener('click', async (event) => {
      const target = event.target;
      if (target.classList.contains('delete')) {
        renderHeaderAndAuthHandler(url);
        const recommendationId = target.closest('tr').getAttribute('data-id');
        try {
          await RecommendationDbSource.deleteRecommendation(recommendationId); // Assume method to delete recommendation
          await fetchAndRenderRecommendations();
        } catch (error) {
          console.error('Error deleting recommendation:', error);
        }
      }
    });

    // Initial fetch and render recommendations
    await fetchAndRenderRecommendations();
  },
};

export default Recommendations;
