import GaleryDbSource from '../../../data/galerydb-source';
import CONFIG from '../../../globals/config';
import UrlParser from '../../../routes/url-parser';
import renderHeaderAndAuthHandler from '../../../utils/renderHeaderAndAuthHandler';

const Galery = {
  async render() {
    return `
      <section class="table-container">
        <div class="table-container-header">
          <h1>Manajemen Galeri</h1>
          <button class="button" id="showFormButton">Menambahkan Galeri</button>
        </div>

        <div class="table-list-container">
          <h2>List Galeri</h2>
          <table>
            <thead>
              <tr>
                <th>Foto</th>
                <th>Nama</th>
                <th>Tanggal</th>
                <th>Aksi</th>
              </tr>
            </thead>
            <tbody id="galeryTable">
              <!-- Galery items will be dynamically inserted here -->
            </tbody>
          </table>
        </div>

        <div id="galeryFormSection" class="input-form">
          <form id="addGaleryForm">
            <div class="form__header">
              <h1>Menambahkan Galeri</h1>
              <button type="button" id="closeFormButton">x</button>
            </div>

            <div class="form__body">
              <div class="form__body__input-group">
                <label for="galeryName">Nama Galeri:</label>
                <input type="text" id="galeryName" placeholder="Masukkan Nama Galeri" required>
              </div>

              <div class="form__body__input-group">
                <label for="galeryDate">Tanggal:</label>
                <input type="date" id="galeryDate" required>
              </div>

              <div class="form__body__input-group">
                <label for="galeryImage">Foto Galeri:</label>
                <input type="file" id="galeryImage" accept="image/*" required>
              </div>

              <div class="form__body__button">
                <button class="button" type="submit">Tambah Galeri</button>
              </div>
            </div>
          </form>
        </div>
      </section>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithCombiner();
    const galeryTable = document.getElementById('galeryTable');
    const galeryFormSection = document.getElementById('galeryFormSection');
    const addGaleryForm = document.getElementById('addGaleryForm');

    // Fetch and render gallery items
    const fetchAndRenderGalery = async () => {
        try {
          const galeryItems = await GaleryDbSource.getAllGaleries();
          galeryTable.innerHTML = galeryItems.map(item => {
            // Ubah tanggal ke format yang lebih mudah dibaca
            const date = new Date(item.date);
      
            // Objek Intl.DateTimeFormat untuk format tanggal dalam bahasa Indonesia
            const options = { year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', minute: 'numeric', };
            const formatter = new Intl.DateTimeFormat('id-ID', options);
            const readableDate = formatter.format(date);
      
            return `
              <tr data-id="${item._id}">
                <td><img src="${CONFIG.BASE_IMAGE_URL + item.imageUrl}" alt="${item.name}"></td>
                <td>${item.name}</td>
                <td>${readableDate}</td>
                <td>
                  <button class="fa-solid fa-trash-can button delete"></button>
                </td>
              </tr>
            `;
          }).join('');
        } catch (error) {
          console.error('Error fetching gallery items:', error);
        }
      };         

    // Event listener to show add gallery form
    document.getElementById('showFormButton').addEventListener('click', () => {
      galeryFormSection.classList.add('open');
    });

    // Event listener to close add gallery form
    document.getElementById('closeFormButton').addEventListener('click', () => {
      galeryFormSection.classList.remove('open');
    });

    // Event listener to handle form submission for adding a gallery item
    addGaleryForm.addEventListener('submit', async (event) => {
      event.preventDefault();
      renderHeaderAndAuthHandler(url);
      const galeryData = {
        name: document.getElementById('galeryName').value,
        date: document.getElementById('galeryDate').value,
        productImage: document.getElementById('galeryImage').files[0],
      };

      try {
        await GaleryDbSource.addGalery(galeryData); // Assume method to add gallery item
        await fetchAndRenderGalery();
        galeryFormSection.classList.remove('open');
      } catch (error) {
        console.error('Error adding gallery item:', error);
      }
    });

    // Event listener to handle deletion of a gallery item
    galeryTable.addEventListener('click', async (event) => {
      const target = event.target;
      if (target.classList.contains('delete')) {
        renderHeaderAndAuthHandler(url);
        const galeryId = target.closest('tr').getAttribute('data-id');
        try {
          await GaleryDbSource.deleteGalery(galeryId); // Assume method to delete gallery item
          await fetchAndRenderGalery();
        } catch (error) {
          console.error('Error deleting gallery item:', error);
        }
      }
    });

    // Initial fetch and render gallery items
    await fetchAndRenderGalery();
  },
};

export default Galery;
