import CONFIG from "../../../globals/config";

const Login = {
  async render() {
    return `
      <section class="auth-form">
        <form id="loginForm">
          <div class="form__header">
            <h1>Login</h1>
          </div>

          <div class="form__body">
            <p class="form__body__error-message"></p>
            <div class="form__body__input-group">
              <label for="username">Username</label>
              <input id="username" type="text" placeholder="Masukkan Username" required>
            </div>

            <div class="form__body__input-group">
              <label for="password">Password</label>
              <input id="password" type="password" placeholder="Masukkan Password" required>
            </div>
          </div>

          <div class="form__body__button">
            <a class="form__body__button__align-end" href="#/forgot-password">Lupa Password?</a>
            <button type="submit" class="button">Masuk</button>
          </div>
        </form>
      </section>
    `;
  },

  async afterRender() {
    const loginForm = document.getElementById('loginForm');

    const loginHandler = async (event) => {
      event.preventDefault();
      const errorMessage = document.querySelector('.form__body__error-message');
      const username = document.getElementById('username').value;
      const password = document.getElementById('password').value;

      try{
        const response = await fetch(`${CONFIG.BASE_URL}admin/login`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ username, password }),
        });
  
        if (response.ok) {
          const result = await response.json();
          const { token } = result.data;
          localStorage.setItem('token', token);
          window.location.hash = '#/products';
        } else {
          errorMessage.style.display = 'block';
          const { message } = await response.json();
          errorMessage.innerText = message;
        }
      } catch (_) {
        errorMessage.style.display = 'block';
        errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
      }
    };
    
    loginForm.addEventListener('submit', loginHandler);
  },
};

export default Login;
