import CONFIG from "../../../globals/config";

const ForgotPassword = {
    async render() {
      return `
        <section class="auth-form">
          <form>
            <div class="form__header">
              <h1>Lupa Password</h1>
            </div>
  
            <div class="form__body">
              <p class="form__body__error-message"></p>
              <p class="form__body__success-message"></p>
              <div class="form__body__input-group">
                <label for="password">Password</label>
                <input id="password" type="password" placeholder="Masukkan Password" required>
              </div>
  
              <div class="form__body__input-group">
                <label for="confirmPassword">Konfirmasi Password</label>
                <input id="confirmPassword" type="password" placeholder="Konfirmasi Password" required>
              </div>

              <div class="form__body__input-group">
                <label for="secretKey">Kata Kunci</label>
                <input id="secretKey" type="password" placeholder="Masukkan Kata Kunci" required>
              </div>
            </div>
  
            <div class="form__body__button">
              <a class="form__body__button__align-end" href="#/login">Halaman Login</a>
              <a id="confirmButton" class="button" href="#">Konfirmasi</a>
            </div>
          </form>
        </section>
      `;
    },
  
    async afterRender() {
      const password = document.getElementById('password');
      const confirmPassword = document.getElementById('confirmPassword');
      const secretKey = document.getElementById('secretKey');
      const confirmButton = document.getElementById('confirmButton');
      const errorMessage = document.querySelector('.form__body__error-message');
      const successMessage = document.querySelector('.form__body__success-message');

      confirmButton.addEventListener('click', async (e) => {
        try {
          e.preventDefault();
          const response = await fetch(`${CONFIG.BASE_URL}admin/change-password`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ password: password.value, confirmPassword: confirmPassword.value, secretKey: secretKey.value }),
          });
  
          const responseJson = await response.json();
  
  
          if (!responseJson.success) {
            errorMessage.style.display = 'block';
            successMessage.style.display = 'none';
            errorMessage.innerText = responseJson.message;
          } else {
            successMessage.style.display = 'block';
            errorMessage.style.display = 'none';
            successMessage.innerText = 'Password berhasil diubah, silahkan ke halaman login';
          }
        } catch (error) {
          errorMessage.style.display = 'block';
          successMessage.style.display = 'none';
          errorMessage.innerText = 'Terjadi kesalahan, coba lagi nanti';
        }
      });
    },
  };
  
  export default ForgotPassword;
  